.modals-container .modal .dialog .dialog-close::after {
  border-color: #9756ff;
}
.modals-container .modal h2,
.modals-container .modal h3 {
  margin: 0 0 16px;
  padding: 0;
}
.modals-container .modal section {
  margin-bottom: 0;
  text-align: center;
}
.modals-container .modal section p {
  font-size: 18px;
  margin: 0;
}
.modals-container .modal section button {
  margin-top: 16px;
}
.modals-container .modal section .tns-outer {
  position: static;
}
.modals-container .modal .carousel img {
  width: 100%;
  max-height: 70vh;
  object-fit: contain;
  display: block;
  border-radius: 15px;
  overflow: hidden;
}
.order {
  text-align: center;
}
.order .order-items {
  margin-bottom: 50px;
  display: flex;
  flex-direction: column;
}
.order .order-items .item {
  width: 100%;
  padding: 1em;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.order .order-items .item .image {
  min-width: 100px;
  max-width: 270px;
  width: 100%;
  padding: 1em;
}
.order .order-items .item .image img {
  box-shadow: 0 3px 5px -1px rgba(0, 0, 0, 0.2), 0 6px 10px 0 rgba(0, 0, 0, 0.14), 0 1px 18px 0 rgba(0, 0, 0, 0.12);
  border-radius: 50%;
}
.order .order-items .item .label {
  text-align: left;
  max-width: 480px;
}
@media (min-width: 550px) {
  .order .order-items {
    flex-direction: column;
  }
  .order .order-items .item {
    flex-direction: row;
  }
}
@media (min-width: 850px) {
  .order .order-items {
    flex-direction: row;
  }
  .order .order-items .item {
    flex-direction: column;
  }
}
.about .container,
.answers .container {
  background: none;
  box-shadow: none;
}
.about h3,
.answers h3 {
  font-size: 3em;
  margin: 1em 0;
}
.about p,
.answers p {
  margin: 2em 0;
}
.about .list,
.answers .list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
.about .list li,
.answers .list li {
  background: white;
  box-shadow: 0 3px 5px -1px rgba(0, 0, 0, 0.2), 0 6px 10px 0 rgba(0, 0, 0, 0.14), 0 1px 18px 0 rgba(0, 0, 0, 0.12);
  border-radius: 20px;
  width: 100%;
  padding: 16px;
  margin: 8px;
}
@media (min-width: 750px) {
  .about .list li,
  .answers .list li {
    width: calc(50% - 16px);
  }
}
@media (min-width: 950px) {
  .about .list li,
  .answers .list li {
    width: 30%;
  }
}
.contacts h3 {
  font-size: 3em;
  margin: 0;
}
