  .contacts ul li {
    width: 33%;
  }
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes cards {
  0% {
    transform: rotate(0);
  }
  25% {
    transform: rotate(3deg);
  }
  50% {
    transform: rotate(0);
  }
  75% {
    transform: rotate(-3deg);
  }
}
@keyframes loadingComplete {
  from {
    min-height: 100vh;
  }
  to {
    min-height: 0vh;
  }
}
